
import {
  defineComponent,
  ref,
} from 'vue'
import { mapGetters } from 'vuex'

export default defineComponent({
  name: 'CustomInput',
  inheritAttrs: false,
  emits: ['input'],
  computed: mapGetters(['text', 'textColors']),

  setup() {
    const root = ref(null);
    const subRoot = ref(null);

    const handleInput = () => {
        // @ts-ignore
		// eslint-disable-next-line no-undef
        OnInputEvent(root.value.innerText);
    }

    return {
      root,
      subRoot,
      handleInput
    }
  },
})

